#topbar {
  width: 100%;
  height: 80px;
  background-color: #8fbcff;

  display: flex;
  .title {
    @import url('https://fonts.googleapis.com/css2?family=Goldman:wght@700&display=swap');
    font-family: 'Goldman', cursive;
    flex: flex;
    flex-direction: column;
    align-self: center;
    font-size: 40px;
    margin-left: 20px;
    font-weight: bold;
    font-style: italic;
  }
}
