@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@700&display=swap);
:root{--box-transition-delay: 0.2s;--box-transition-function: linear}.grid-board{display:flex;flex-wrap:wrap;width:100%}.row{width:100%;display:flex;justify-content:center}.box{padding:1px;display:flex;width:25px;height:25px;border:0.001em solid #57dbff;transition:background-color 0.2s linear;transition:background-color var(--box-transition-delay) var(--box-transition-function);border-right:none;border-bottom:none}.box:last-child{border-right:0.001em solid #57dbff}.row:last-child .box{border-bottom:0.001em solid #57dbff}.row:last-child .wall{border-bottom:0.001em solid #ff80f2}.board-body{width:100%}.box.wall{background:#ff80f2}.box.start-point{background:#0400ff}.box.end-point{background:yellow}.box.hover{background-color:#57dbff}.box.wall.hover{background-color:#80b3ff}.box.visited{background:#3f5efb;animation-name:visitedAnimation;animation-duration:1s;animation-timing-function:ease-out;animation-delay:0;animation-direction:alternate;animation-iteration-count:1;animation-fill-mode:forwards;animation-play-state:running}@keyframes visitedAnimation{0%{transform:scale(0.3);background-color:#4bcae0;border-radius:100%}50%{background-color:rgba(50,217,17,0.75)}75%{transform:scale(1.2);background-color:rgba(180,238,23,0.75)}100%{transform:scale(1);background-color:#28f7db}}.box.path{background:#ff3737}#topbar{width:100%;height:80px;background-color:#8fbcff;display:flex}#topbar .title{font-family:'Goldman', cursive;flex:flex 1;flex-direction:column;align-self:center;font-size:40px;margin-left:20px;font-weight:bold;font-style:italic}#controller{margin:24px auto;display:flex;justify-content:center}#controller .left{display:flex}#controller .right{display:flex}#controller .button-wrap .button,#controller .button-wrap .select-container{width:100px;height:50px;font-size:18px;border-radius:0px;margin:2px;background-color:#ffffff}#controller .button-wrap .button :focus,#controller .button-wrap .select-container :focus{outline:unset}#controller .counter{display:flex}#controller .counter-box{width:100px;height:50px;border:3px solid red;box-sizing:border-box;margin:2px;display:flex;font-weight:bold}#controller .counter-box .elapsed-time{width:100%;display:flex;justify-content:center;align-self:center}#controller .counter-box .move-counter{width:100%;display:flex;justify-content:center;align-self:center}#controller .select-container{border:2px solid black}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}main{margin:0 auto 80px}

